
/* Sizes
Using Lucas Numbers 1, 3, 4, 7, 11, 18, 29, 47, 76, 
---------------------------------------------------------------------------- */
$faktor: 1.1;
var(--xxs): 4px * $faktor;
$xs: 7px * $faktor;
$s: 11px * $faktor;
$m: 18px * $faktor;
$l: 29px * $faktor;
$xl: 47px * $faktor;
var(--xxl): 76px * $faktor;


/* Font Properties
---------------------------------------------------------------------------- */
var(--font-family-serif): 'FiraItalic', 'Courier New', monospace;
$font-family-sans: Fira, sans-serif;
$base-font-size: var(--m);
$font-icons: 'Material Icons';

/* Font Weights
---------------------------------------------------------------------------- */
$fw-light: 200;
$fw-normal: 300;
$fw-medium: 500;
$fw-bold: 700;


/* Line Heights
---------------------------------------------------------------------------- */
$lh-tight: 110%;
var(--lh-default): 140%;
$lh-loose: 150%;


/* Opacity
---------------------------------------------------------------------------- */
$op-medium:0.6;


/* Base Colors
---------------------------------------------------------------------------- */
:root{
  --tileColor: #8C775E;
}

$steingrau: hsla(42, 3%, 55%, 0.6);
$lichtgrau: hsla(100, 3%, 77%, 1);
$hellgrau: #BBBAB7;
$blaugrau: #82899A;
$pink: #dd1166;
var(--green): #10CFA3;
$light: hsla(0, 0%, 70%, 1); //#E6DED1;
var(--lighter);: hsla(0, 0%, 80%, 1);
$lightest: hsla(0, 0%, 100%, 1);
var(--lighten): rgba(255,255,255,0.2);
var(--dark): hsla(0, 0%, 10%, 1); //#57544F;
var(--darker): #000;
var(--dark)en: rgba(0,0,0,0.2);


/* Functional Colors
---------------------------------------------------------------------------- */
$interaction-color: var(--lighter);;
$decoration-color: var(--pink);
$text-color: var(--light);
var(--color-headline): var(--light);
var(--color-headline)-light: var(--lighter);;
$aside-color: $lichtgrau;
$border-color: $lichtgrau;
$seperator-color: var(--light);
$background-color: var(--dark);


/* Transitions
---------------------------------------------------------------------------- */
var(--tr-fast): 0.2s;
var(--tr-medium): 0.6s;
var(--tr-slow): 1s;

/* Opacities
---------------------------------------------------------------------------- */
$op-strong: 0.75;
$op-medium: 0.5;


/* Viewports
---------------------------------------------------------------------------- */
20rem: 688px;
40rem: 800px;
$vp-large: 1100px;
$vp-xlarge: 1400px;
$shadow-m: 0 0 10px rgba(0, 0, 0, 0.2);

/* Custom Properties
---------------------------------------------------------------------------- */
:root{
  --section-space: calc(#{$m} + 10vw);
  
  /* Fluid Spaces
  ---------------------------------------------------------------------------- */
  --fluid-space-xs: 1vw;
  --fluid-space-s: 2vw;
  --fluid-space-m: 4vw;
  --fluid-space-l: 8vw;
  --fluid-space-xl: 16vw;

  /* Colors
  ---------------------------------------------------------------------------- */

  --color-title: #{$light};
  --color-lighten: #{var(--lighten)};
  --color-headline: #{var(--color-headline)};
  --color-links: #{var(--lighten)};
  --color-hero: #{var(--green)};
}

/* Misc
---------------------------------------------------------------------------- */
$bw: 2px;
$text-shadow: 0 0 20px rgba(0,0,0,0.9);
$max-width-content: 50rem;
var(--max-width-text): $vp-large;
$aside-width: 20rem;
$border-width: 2px;
$border-width-s: 1px;
var(--border-radius): 4px;