.text{
  max-width: 120ch;
  padding-right: var(--xxl);

  .herotext{
    margin-top: var(--xl);
    margin-bottom: var(--xxl);
    color: var(--color-hero);
  }

  ul{
    li{
      margin-left: 2ch;
      &::before{
        content: "»";
        display: inline-block;
        margin-left: -2ch;
        padding-right: 0.5ch;
        color: var(--green);
        width: 1.5ch;
      }
    }

    padding-bottom: var(--l);
  }

  ol{
    li{
      margin-bottom: var(--s);
    }

    padding-bottom: var(--l);
  }

  .block{
    margin-bottom: var(--l);
  }

  p{
    margin-bottom: var(--m);;
  }

  hr{
    background-color: transparent;
    border: none;
    border-top: dotted var(--green) 3px;
    margin-top: var(--xl);
    margin-bottom: var(--xl);
  }

  h2{
    font-size: var(--xl);
  }

  h3{
    font-size: var(--l);
    font-weight: var(--fw-normal);
    margin-bottom: var(--l);
  }

  h4{
    padding-top: var(--s);
  }

  .date{
    font-size: var(--m);
  }

  a{
    font-weight: var(--fw-normal);
  }

}

/* Playground
###################################################################################### */

.layout-playground{

  figure{
    padding-right: var(--xl);

    img + img{
      padding-top: var(--fluid-space-m);
    }
  }

  .darken{
    mix-blend-mode: darken;
  }
}

/* Vita Filter
###################################################################################### */

.filter{
  display: inline-flex;
  gap: var(--xs);

  &-button, &-button:active, &-button:focus{

    color: var(--color-interaction);
    font-size: var(--m);
    border-radius: var(--border-radius);
    border: solid 2px var(--lighten);
    padding: var(--xxs) var(--xs);
    transition: all var(--tr-fast);

    &:hover{
      cursor: pointer;
      border-color: var(--color-decoration);
    }

    &:active, &.is-active{
      background-color: var(--color-decoration);
    }
  }
  
}

.filter-items{

  & > * {
    overflow: hidden;
    max-height: 20rem;
    transition: max-height var(--tr-medium) ease-in-out, margin var(--tr-medium) ease-in-out;
    margin-bottom: var(--xs);
  }

  .is-hidden{
    max-height: 0;
    margin-bottom: 0;
  }
}

.vita{

  &-item-title{
    text-align: right;
  }

  &-item-data{
    font-weight: var(--fw-normal);
  }

  &-text{
    max-height: 0;
    overflow: hidden;
  }
}