@media only screen and (min-width: 40rem) {
  .header,
  .page,
  .footer {
    // max-width: $vp-xlarge;
  }
  p, blockquote {
    max-width: var(--max-width-text);
  }
}

// Page
// ------------------------------------------------------------------------- //
.page {
  padding-bottom: var(--xl);

  @media only screen and (min-width: 40rem) {
    padding-left: var(--xl);
  }
}

// Header
// ------------------------------------------------------------------------- //
.header {
  padding: var(--s);
  text-align: center;

  @media only screen and (min-width: 40rem) {
    display: grid;
    grid-template-columns: 200px 1fr;
    gap: var(--m);
    align-items: center;
    text-align: left;
    padding: var(--l);
    padding-left: var(--xl);
  }

  a{
    border: none;
    color: var(--color-decoration);
  }

  .title {
    font-size: var(--xl);
    font-weight: var(--fw-medium);
    color: var(--color-title);
    margin-bottom: var(--m);
  }
  .subtitle,
  .subtitle a {
    font-size: var(--m);
    font-weight: 300;
  }

}

// Section
// ------------------------------------------------------------------------- //
.section {
  padding: var(--s);
  @media only screen and (min-width: 40rem) {
    padding: var(--l);
  }
}

// Contact
// ------------------------------------------------------------------------- //
.contact {}

// Toc
// ------------------------------------------------------------------------- //
.toc {
  &__item {
    padding-top: var(--xs);
    padding-bottom: var(--xs);
    border-top: solid 1px var(--color-seperator);
    max-width: var(--max-width-text);
  }
}

// Footer
// ------------------------------------------------------------------------- //
.footer {
  background-color: var(--dark);
  color: var(--lighter);;
  padding: var(--s);
  padding-bottom: var(--xl);
  @media only screen and (min-width: 40rem) {
    padding: var(--xl);

    & > div{
      margin-left: var(--l);
    }
  }
  a {
    color: var(--lighter);;
  }
}

// Image Block
// ------------------------------------------------------------------------- //
// Content Block
// ------------------------------------------------------------------------- //
// Canvas
// ------------------------------------------------------------------------- //
#canvas {
  width: 100vw;
  height: 600px;
}

// Gallery
// ------------------------------------------------------------------------- //
.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--l);
  padding-top: var(--s);
  margin-bottom: var(--l);
  &__item {
    position: relative;
    overflow: hidden;
    
    &.is16by9{
      aspect-ratio: 16/9;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    iframe{
      width: 100%;
      aspect-ratio: 16/9;
    }

    figcaption{
      padding-top: var(--xs);

      h3{
        font-size: var(--m);
        font-weight: var(--fw-bold);
      }
    }

  }
}

// Herotext
// ------------------------------------------------------------------------- //
.herotext {
  font-size: var(--m);
  font-weight: var(--fw-normal);
  line-height: var(--lh-default);
  @media only screen and (min-width: 40rem) {
    font-size: var(--l);
    font-weight: var(--fw-normal);
  }
}

// Page Title
// ------------------------------------------------------------------------- //

.page-title{
  font-size: var(--xl);
  max-width: var(--max-width-text);
}

// brutal
// ------------------------------------------------------------------------- //
.brutal{
  font-size: clamp(2em, calc(2em + 10vw), 20em);
  font-weight: 600;
  color: var(--lighten);
  margin-left: -1vw;
}

.semi-brutal{
  font-size: clamp(1.2em, calc(2em + 5vw), 5em);
  font-weight: 600;
  color: var(--lighten);
  margin-left: -1vw;
}

// Tabelle
// ------------------------------------------------------------------------- //
table {
  table-layout: fixed;
  margin-bottom: var(--xl);
  width: 100%;
  th,
  td {
    text-align: left;
    font-size: var(--m);
    padding: var(--xxs);
    padding-right: var(--l);
    border-top: solid 1px var(--color-seperator);
    line-height: var(--lh-default);
  }
  td {
    font-weight: var(--fw-light);
    width: 240px;
    text-align: right;
  }
  th {
    width: 80%;
    font-weight: var(--fw-medium);
  }
  tfoot {
    tr {
      th,
      td {
        font-weight: var(--fw-bold);
      }
    }
  }
  @media only screen and (min-width: $vp-large) {
  }
}

// Taglist
// ------------------------------------------------------------------------- //
.taglist {
  margin-bottom: 0;

  &--linear{
    margin-top: var(--m);

    li {
      display: inline-block;
      padding-right: var(--s);
      margin-bottom: 0;
    }
  } 

  &--small{
    font-size: calc($m * 0.8);
  
    a{
      border-bottom: none;
    }
  }

  &__item {
    &::before {
      content: '#';
      color: var(--color-decoration);
    }
  }
}

// Avatar
// ------------------------------------------------------------------------- //
.avatar {
  border-radius: 50%;
  width: 100%;
  max-width: 300px;
}

// Course Overview
// ------------------------------------------------------------------------- //
.course-overview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  row-gap: var(--m);
  column-gap: var(--m);
  padding-top: var(--s);
  margin-bottom: var(--l);

  &__item {
    position: relative;
    overflow: hidden;
    font-weight: var(--fw-light);
    color: var(--dark);
    background-color: var(--light);
    padding: $m $m $l var(--m);
    transition: background-color var(--tr-fast);
    display: flex;
    flex-direction: column;

    &:hover {
      background-color: var(--lightest);
      cursor: pointer;
    }
    a {
      font-weight: var(--fw-medium);
      color: var(--dark);
    }
    .course-overview__item-headline a {
      color: var(--darker);
      font-weight: var(--fw-medium);
      border: none;
    }
  }
}

// Feed Overview
// ------------------------------------------------------------------------- //

.feed{
  &-overview{
    padding-top: var(--s);
    margin-bottom: var(--l);
  }
  
  &-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    row-gap: var(--m);
    column-gap: var(--m);
  }
  
  &__item {
    position: relative;
    overflow: hidden;
    font-weight: var(--fw-light);
    color: var(--light);
    transition: transform var(--tr-medium);
    display: flex;
    flex-direction: column;
    background-color: var(--darker);

    & > * {
      transform: scale(0.5);
      transition: transform var(--tr-medium);
    }

    &:hover {

      & > * {
        transform: scale(1.0);
      }
      // transform: translate(10ch);
      cursor: pointer;

      .info{
        padding: $xs $m $m var(--m);
      }
    }
    a {
      font-weight: var(--fw-medium);
      color: var(--light);
    }
    &-headline a {
      color: var(--darker);
      font-weight: var(--fw-bold);
    }

    .info{
      padding: $xs 0 $m 0;
      transition: padding var(--tr-fast);
    }

    img{
      aspect-ratio: 1/1;
      width: 100%;
      object-fit: cover;
    }
  }
}

// Image Stripe
// ------------------------------------------------------------------------- //

.image-stripe{
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;

  & > img{
    display: block;
  }
}


// Content
// ------------------------------------------------------------------------- //

.content{
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;

}


// Check it out
// ------------------------------------------------------------------------- //
.check-it-out{



  &__item{
    margin-bottom: var(--l);

    blockquote + figcaption{
      margin-top: -var(--s);
      margin-bottom: var(--s);
      &::before{
        content: "— ";
        color: var(--lighten);
        font-weight: bold;
      }
    }

    a{
      font-weight: 300;
      border-width: var(--border-width-s);
    }

    & + &{
      &::before{
        content: ".";
        display: block;
        color: var(--green);
        font-weight: bold;
        margin-bottom: var(--l);
      }
    }
  }
}

// Is Somewhere
// ------------------------------------------------------------------------- //
.is-somewhere{

  @media only screen and (min-width: 40rem) {
    display: flex;
    gap: var(--l);
    padding-right: clamp(1em, 10vw, 20em);

    h2{
      flex-grow: 1;
      text-align: right;
    }
  }
}

// date
// ------------------------------------------------------------------------- //

h2 + .date{
  margin-top: -var(--m);
  font-weight: var(--fw-medium);
}


// Outstanding Content
// ------------------------------------------------------------------------- //

.outstanding{
  margin-top: var(--xl);
  background-color: var(--color-decoration);
  color: var(--lightest);
  padding: var(--l);
  width: fit-content;
  border-radius: var(--border-radius);
}

// Book Overview
// ------------------------------------------------------------------------- //
.book-overview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  row-gap: var(--m);
  column-gap: var(--m);
  padding-top: var(--s);
  margin-bottom: var(--l);

  .book{
    &-img{
      border: none;
      background-color: var(--lighten);
      display: block;
      padding: var(--s);
      img{
        aspect-ratio: 1/1;
        object-fit: contain;
      }
    }

    h3{
      font-weight: var(--fw-medium);
      margin-top: var(--s);
      line-height: var(--lh-default);
    }

    .tight-list{
      li{
        margin-bottom: 0;
        font-size: $m * 0.8;
        line-height: var(--lh-tight);
      }
    }

    .cta{
      font-size: $m * 0.8;
    }
  }
}

// Featured
// ------------------------------------------------------------------------- //

.featured{
  padding: var(--fluid-space-l);

  h3{
    font-weight: var(--fw-medium);
  }
}


// Columns
// ------------------------------------------------------------------------- //

[data-columns="2"]{
  display: grid;
  row-gap: var(--m);

  @media only screen and (min-width: 40rem) {
    column-gap: var(--m);
    padding-top: var(--s);
    margin-bottom: var(--l);
    grid-template-columns: 1fr 1fr;
  }
}

// Main Nav
// ------------------------------------------------------------------------- //

nav#main{
  margin-top: 10vw;
  grid-column: span 2; 
  ul{
    li{
      line-height: var(--lh-tight);
      a{
        color: var(--color-link);
        font-size: var(--l);
        display: block;
        line-height: var(--lh-tight);
      }
    }
  }

  @media only screen and (min-width: 40rem) {
    padding-left: var(--xl);
    ul{
      li{
        line-height: var(--lh-tight);
        a{
          font-size: var(--xl);
          display: inline-block;
          padding-right: 0.5ch;
          &::before{
            content: "»";
            color: var(--pink);
            display: inline-block;
            padding-right: 0.25ch;
            transition: all var(--tr-fast);
          }
  
          &:hover::before{
            transform: rotate(180deg);
            padding-right: 0.5ch;
            padding-left: 0.5ch;
            color: var(--darker);
          }
        }
      }
    }
  }

}

// CTA
// ------------------------------------------------------------------------- //
/* CSS */

.cta {
  position: relative;
  overflow: hidden;
  border: 1px solid #18181a;
  color: #fff;
  display: inline-block;
  line-height: var(--lh-tight);
  padding: $s*0.8 var(--s);
  text-decoration: none;
  cursor: pointer;
  background: var(--pink);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.cta span:first-child {
  position: relative;
  transition: color 600ms cubic-bezier(0.48, 0, 0.12, 0.5);
  z-index: 10;
}

.cta span:last-child {
  color: var(--pink);
  display: block;
  position: absolute;
  bottom: 0;
  transition: all 500ms cubic-bezier(0.48, 0, 0.12, 1);
  z-index: 100;
  opacity: 0;
  top: 50%;
  left: 50%;
  transform: translateY(225%) translateX(-50%);
  height: 12px;
  line-height: 26px;
}

.cta:after {
  content: "";
  position: absolute;
  bottom: -50%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  transform-origin: bottom center;
  transition: transform 600ms cubic-bezier(0.48, 0, 0.12, 1);
  transform: skewY(9.3deg) scaleY(0);
  z-index: 50;
}

.cta:hover:after {
  transform-origin: bottom center;
  transform: skewY(9.3deg) scaleY(2);
}

.cta:hover span:last-child {
  transform: translateX(-50%) translateY(-100%);
  opacity: 1;
  transition: all 900ms cubic-bezier(0.48, 0, 0.12, 1);
}


// Grid List
// ------------------------------------------------------------------------- //

.grid-list{
  dt{
    font-weight: var(--fw-normal);
    color: var(--green);
    &::after{
      content: "";
    }
  }



  @media screen and (min-width: 20rem) {
    display: grid;
    grid-template-columns:max-content 1fr;
    column-gap: var(--m);

  
  }
}