// Icons
.icon {
  height: auto;
  width: auto;
  font-family: $font-icons;
  vertical-align: middle;
  font-weight: normal;
  font-style: normal;
  display: inline-block;
  line-height: inherit;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: 'liga';
  margin-right: var(--xxs);
}


// Sizes
.icon {
  &--s { font-size: var(--s); line-height: var(--s); }
  &--m { font-size: var(--m); line-height: var(--m);}
  &--l { font-size: var(--l); line-height: var(--l);}
  &--xl { font-size: var(--xl); line-height: var(--xl);}
}

.icon {
  &.md-dark { color: var(--dark); }
  &.md-light { color: var(--light); }

}

.icon:before {
   content: attr(data-icon);
}

.icon {
  &.red {color: red;}
}

//.icon Font Awesome Extend
.icon {
  font-family: $font-icons;
  text-transform: none;
}


// Modifiers
.icon{

  &--inline{
    margin-right: 0.4em;
    font-size: var(--m);
  }

}
